body {
    margin: 0;
}
#map {
    height: 100%;
}
.map-overlay {
    position: absolute;
    z-index: 100;
    background-color: rgba(255, 255, 255, 0.7);
    left: 0;
    right: 0;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#map {
    position:absolute;
    top:0;
    bottom:0;
    width:100%;
}

#re-render {
    position: absolute;
    z-index: 2;
}